import { useFormik } from 'formik'
import * as Yup from 'yup';
import React, { useState } from 'react'
import Footer from '../../components/Footer/Footer'
import Header from '../../components/Header/Header'
import { DOMAIN_FIVERR, TOKEN, USERLOGIN } from '../../util/setting';
import axios from 'axios';

export default function Register(props) {

    if (localStorage.getItem(USERLOGIN)) {
        props.history.push("/");
    }

    const [errorMessage, setErrorMessage] = useState('');

    const formik = useFormik({
        // gi tr khi to (khai bo i tng cn lu tr data)
        // t tn thuc tnh i tng ging theo BE quy nh
        initialValues: {
            email: '',
            password: '',
            name: '',
        },
        validationSchema: Yup.object({
            password: Yup.string().required("Mt khu khng c  trng"),
            passwordConfirmation: Yup.string().required("Mt khu khng c  trng").oneOf([Yup.ref('password'), null], 'Mt khu phi trng nhau'),
            email: Yup.string().required("Email khng c  trng").email("Email cha ng nh dng"),
            name: Yup.string().required("H tn khng c  trng").matches(/^[A-Z a-z]+$/, "H tn khng ng nh dng")
        }),
        //! d liu t form s c lu vo values

        onSubmit: values => {
            let promise = axios ({
                url: `${DOMAIN_FIVERR}/auth/signup`,
                method: "POST",
                data: values,
                headers: {
                  "TokenCybersoft": TOKEN
                }
            });
            promise.then(result => {
                setErrorMessage('');
                props.history.push("/login");
            });
            promise.catch(error => {
                setErrorMessage(error.response.data.content)
            })
        },
    });

    return (
        <>
            <Header />

            <div className="main-content">
                <div className="mp-box-min-height">
                    <section className='standalone'>
                        <div className="entry">
                            <form onSubmit={formik.handleSubmit}>
                                <div className="sign-in-form">
                                    <section className='form-header'>
                                        <h4>Join Fiverr</h4>
                                    </section>

                                    <div className="field">
                                        <div className="field-input-wrapper">
                                            <input name='email' className='field-input' type="text" placeholder="Email / Username" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email} />
                                            {/* thng bo li  */}
                                            {formik.touched.email && formik.errors.email ? (
                                                <div className='error-alert'>{formik.errors.email}</div>
                                            ) : null}
                                            <p className='error-alert'>{errorMessage}</p>
                                        </div>
                                    </div>

                                    <div className="field">
                                        <div className="field-input-wrapper">
                                            <input name='password' className='field-input' type="password" placeholder="Mt khu" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                                            {/* thng bo li  */}
                                            {formik.touched.password && formik.errors.password ? (
                                                <div className='error-alert'>{formik.errors.password}</div>
                                            ) : null}
                                        </div>
                                    </div>

                                    <div className="field">
                                        <div className="field-input-wrapper">
                                            <input name='passwordConfirmation' className='field-input' type="password" placeholder="Nhp li mt khu" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.passwordConfirmation} />
                                            {/* thng bo li  */}
                                            {formik.touched.passwordConfirmation && formik.errors.passwordConfirmation ? (
                                                <div className='error-alert'>{formik.errors.passwordConfirmation}</div>
                                            ) : null}
                                        </div>
                                    </div>

                                    <div className="field">
                                        <div className="field-input-wrapper">
                                            <input name='name' className='field-input' type="text" placeholder="H tn" onChange={formik.handleChange} onBlur={formik.handleBlur} />
                                            {/* thng bo li  */}
                                            {formik.touched.name && formik.errors.name ? (
                                                <div className='error-alert'>{formik.errors.name}</div>
                                            ) : null}
                                        </div>
                                    </div>

                                    <button type='submit' className='submit-button'>
                                        <p>Register</p>
                                    </button>
                                </div>
                            </form>

                            <div className="signing-footer">
                                <p>Already a member?</p>
                                <a href="/login" className='link-join'>Sign In</a>
                            </div>
                        </div>
                    </section>
                </div>
            </div>

            <Footer />
        </>
    )
}
